export default {
  data() {
    return {
      modelStatus: false, // 登录注册页面出现状态
      userStatus: false, // 用户登录状态
      isLogin: true, // 登录/注册 切换
      // 登录输入框动画
      loginAnimate: {
        uNameActive: false,
        uPwdActive: false
      },
      // 登录状态账号密码绑定
      loginForm: {
        uName: '',
        uPwd: ''
      },
      // 注册状态输入框动画
      regAnimate: {
        uNameActive: false,
        uEmailActive: false,
        uPwdActive: false,
        repeatPwd: false
      },
      // 注册状态信息绑定
      regForm: {
        uName: '',
        uEmail: '',
        uPwd: '',
        uRepeat: ''
      }
    };
  },
  onTabItemTap() {
    if (!this.userStatus) {
      uni.hideTabBar({ animation: true });
    }
    this.userStatus = this.$store.state.userStatus;
    this.modelStatus = !this.$store.state.userStatus;
  },
  onShow() {},
  methods: {
    // 登录
    login() {
      // 正则验证...
      this.showToast('密码不正确');

      // 登录成功
      this.successLogin();
    },

    // 注册并登录
    register() {
      // 正则验证...
      this.showToast('请输入正确的邮箱');

      // 注册并登录成功
      this.successLogin();
    },

    // 取消登录/注册
    closeModel() {
      this.modelStatus = false;
      uni.showTabBar({ animation: true });
      this.jump('/pages/information/information');
    },

    // 登录/注册并登录 成功
    successLogin() {
      // 登录成功够关闭 登录/注册 框
      this.modelStatus = false;
      // 登录成功 -> 更新用户登录状态
      this.userStatus = this.$store.state.userStatus = true;
      uni.showTabBar({ animation: true });
    },

    // 退出登录
    escLogin() {
      let _this = this
      uni.showModal({
          title: '温馨提示',
          content: '确认退出登录吗？',
          success: function (res) {
              if (res.confirm) {
                  // console.log('用户点击确定');
                  _this.userStatus = _this.$store.state.userStatus = false;
                  _this.jump('/pages/information/information');
              } else if (res.cancel) {
                  // console.log('用户点击取消');
              }
          }
      });
      
    },

    // 去微信登录
    wxLogin() {
      this.jump('/pages/user/login/login')
    }
  }
};
